.jumbotron-img {
  background-image: url('https://i.imgur.com/LTb6vhk.png');
  max-height: 100%;
  max-width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  color: black;
  display: -ms-flexbox;
  display: flex;
}

.jumbo-title {
  font-size: 90px;
  background-color: rgba(255, 255, 255, 0.2);
  padding-top: 100px;
  margin-bottom: 0;
  padding-left: 50px;
  margin-top: 0;
  
}

.p-jumbo {
  padding-bottom: 280px;
  padding-top: 10px;
  background-color: rgba(255, 255, 255, 0.2);
  padding-left: 50px;
  margin: 0 auto;
  width: 100vw;

}




